/* First party */
import { Component } from 'react'
import { Grid, Row, Col } from 'react-styled-flexboxgrid'

/* Third party */
import Searchbar from '../components/Searchbar'
import styled from 'styled-components'

const Container = styled.div`
    margin: 0 auto;
`

const Title = styled.h1`
    color: lightblue;
    text-align: center;
`

const Theme = styled(Grid)`
    font-family: 'NeueHaasUnicaPro-Regular';
`
const apiKey = 'G3kX7Y9C74tjpHZadKuIGqjwLrCmfZnU'

export default class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {
            page: 'home',
            searchTerm: '',
            results: [],
            favorites: [],
        }
    }

    handleSearchTermChange = (event) => {
        this.setState({
          searchTerm: event.target.value
        })
      }    

    handleSearchbarSubmit = (searchTerm) => {
        console.log('in handlesubmit: ', searchTerm)
        let urlRequest = `http://api.giphy.com/v1/gifs/search?q=${this.state.searchTerm}&api_key=${apiKey}&limit=5`
        fetch(urlRequest)
            .then(res => res.json())
            .then(result =>
                console.log('result: ', result)
            )
    }

    componentDidMount() {
        let urlRequest = `http://api.giphy.com/v1/gifs/search?q=pandas&api_key=${apiKey}&limit=5`
        fetch(urlRequest)
            .then(res => res.json())
            .then(result =>
                console.log(result)
            )
    }
    
    render () {
        const { searchTerm } = this.state
        return (
            <Container>
                <link href="https://fonts.googleapis.com/icon?family=Material+Icons"
        rel="stylesheet"></link>
                <Title>  </Title>
                <Theme>
                    <Row center='xs'>
                        <Col xs={6}>
                            <Searchbar 
                                searchTerm={searchTerm} 
                                onChange={this.handleSearchTermChange}
                                onSubmit={this.handleSearchbarSubmit} />
                        </Col>
                    </Row>
                </Theme>
            </Container>
        )
    }
}
